.homeBackground {
  min-height: 100vh;
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.homeContainer {
  height: auto;
  width: 85%;
  margin: 80px auto 0px auto;
  /* border: 1px solid black; */
  padding: 100px 0px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}
.homeContainer .rightDiv {
  height: auto;
  width: 85%;
  /* border: 1px solid black; */
}
.homeContainer .leftDiv {
  height: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  padding: 0px 0px 0px 40px;
}
.leftDiv h1 {
  font-size: 2.8rem;
  font-weight: 600;
  text-align: left;
}
.leftDiv p {
  font-size: 1.3rem;
  font-weight: 600;
  color: #9e979e;
  margin: 10px 0px 0px 10px;
}
.rightDiv img {
  width: 500px;
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes animate {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
.homeBtn {
  font-size: 1.1rem;
  color: rgb(252, 45, 45);
  font-weight: 600;
  background: transparent;
  padding: 5px 20px;
  border: 3px solid rgb(252, 45, 45);
  border-radius: 5px;
  cursor: pointer;
  margin: 20px 0px 0px 50px;
  position: relative;
  z-index: 1;
}

.homeBtn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: rgb(252, 45, 45);
  z-index: -1;
}
.homeBtn:hover {
  color: #fff;
  transition: all 0.5s ease;
}
.homeBtn:hover::before {
  transition: all 0.5s ease;
  width: 100%;
}

